* {
  padding: 0;
  margin: 0;
}

html, body {
  font-family: "Fira Sans";
  overflow-x: hidden;
}

.container {
  box-sizing: border-box;
  width: 100% !important;
  height: 100%;
  max-width: 1140px;
  position: relative;
}

@media only screen and (max-width: 1024px) {
  .container {
    overflow-x: hidden;
    padding: 0 10px;
  }
}

.row {
  height: 100%;
}

a {
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
  transition: all 0.3s;
}

p {
  font-size: 16px;
}

.heading {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading h2 {
  font-size: 36px;
  font-weight: bold;
  text-align: left;
  color: #3c4450;
  padding-top: 60px;
  padding-bottom: 30px;
}

.heading p {
  font-size: 22px;
  color: #707984;
  padding-top: 10px;
  padding-bottom: 80px;
}

.heading--discription {
  position: relative;
  padding-bottom: 80px;
}

.heading--discription h2:before {
  content: '';
  position: absolute;
  bottom: 40px;
  width: 46px;
  height: 5px;
  left: 0;
  background: linear-gradient(to bottom, #42d79f, #27c8ca);
  border-radius: 5px;
}

.btn {
  padding: 15px 25px;
  border-radius: 4px;
  display: inline-flex;
  border-style: solid;
  border-width: 3px;
  border-image-source: linear-gradient(to bottom, #42d79f, #27c8ca);
  border-image-slice: 1;
  font-weight: bold;
}

.btn--discription {
  color: #3c4450;
  margin-right: 30px;
  margin-bottom: 20px;
}

@media only screen and (max-width: 600px) {
  .btn--discription {
    padding: 15px 0;
    width: 100%;
    justify-content: center;
  }
}

.btn--discription i {
  position: relative;
  height: 20px;
  padding-right: 15px;
  color: #000;
  background: #fff;
}

.btn--discription i img {
  height: 20px;
  width: 30px;
}

.btn--ads {
  color: #3c4450;
  margin-top: 50px;
  margin-bottom: 70px;
}

@media only screen and (max-width: 767px) {
  .btn--ads {
    padding: 15px 0;
    width: 100%;
    justify-content: center;
    font-size: 20px;
  }
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 55;
  border-bottom: 1px solid #95959c;
}

.header__block {
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .header__block--mobile {
    text-align: center;
  }
}

.header__block__logo {
  width: 165px;
}

.header__block__logo img {
  width: 100%;
}

@media only screen and (max-width: 767px) {
  .header__block__logo img {
    max-width: 300px;
  }
}

.header__block__location {
  font-size: 16px;
  line-height: 2.25;
  color: #ffffff;
  position: relative;
  z-index: 9999;
  display: flex;
  justify-content: center;
}

.header__block__location p {
  display: inline-block;
  padding: 30px 0px;
  padding-left: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.header__block__location p:hover {
  color: #42d79f;
}

.header__block__location p i {
  position: relative;
  font-size: 20px;
  right: -15px;
  top: 2px;
  transition: all 0.3s;
}

.header__block__location__menu {
  display: none;
  position: absolute;
  top: 70px;
  background-image: linear-gradient(to bottom, #42d79f, #26c8cb);
  margin-left: 10px;
}

.header__block__location__menu a {
  display: block;
  padding: 10px;
  border-bottom: 1px solid #95959c;
}

.header__block__location__menu a:hover {
  color: #3c4450;
  border-bottom: 1px solid #42d79f;
}

.header__block__location.active p {
  color: #42d79f;
}

.header__block__location.active i {
  transform: rotate(180deg);
  color: #42d79f;
}

.header__block__location.active .header__block__location__menu {
  display: block;
}

.header__block__get a {
  padding: 27px 25px;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  line-height: 2.25;
  color: #ffffff;
  position: relative;
  top: 1px;
}

.header__block__get a.active {
  border-bottom: 3px solid #42d79f;
}

.header__block__user {
  padding: 35px 25px;
  display: inline-block;
}

.intro {
  background: url("../img/header__bg.png") no-repeat;
  background-size: cover;
  padding: 180px 0;
  padding-top: 220px;
  position: relative;
}

@media only screen and (max-width: 767px) {
  .intro {
    padding-top: 350px;
  }
}

.intro h1 {
  font-size: 36px;
  font-weight: bold;
  line-height: 1.28;
  color: #ffffff;
  padding-bottom: 40px;
}

.intro form {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 1024px) {
  .intro form {
    display: block;
  }
}

.intro form select {
  width: 100%;
  background: #ffffff;
  border-radius: 0;
  border: none;
  padding: 25px 0;
  font-size: 15px;
  text-align-last: center;
  text-align: center;
}

.intro form select:firs-child {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.intro form input {
  padding: 25px 75px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-image: linear-gradient(to bottom, #42d79f, #26c8cb);
  font-family: Roboto;
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  outline: none;
  transition: all 0.3s;
  cursor: pointer;
}

.intro form input:hover {
  color: #3c4450;
}

@media only screen and (max-width: 1024px) {
  .intro form input {
    width: 100%;
    padding: 25px 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 5px;
  }
}

.intro__btn {
  position: absolute;
  width: 100%;
  bottom: 0;
}

.intro__btn a {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.intro__btn a:hover i {
  color: #42d79f;
}

.intro__btn i {
  font-size: 40px;
  color: #ffffff;
}

.description {
  padding-top: 90px;
  padding-bottom: 60px;
  position: relative;
}

.description:before {
  content: '';
  width: 983px;
  height: 983px;
  background-image: linear-gradient(to bottom, #f2f5fa, #ffffff);
  position: absolute;
  left: -41%;
  top: 170px;
  border-radius: 100%;
  z-index: 1;
}

@media only screen and (max-width: 1024px) {
  .description:before {
    display: none;
  }
}

.description__content {
  max-width: 740px;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.description__content__video img {
  width: 100%;
}

.description__content p {
  line-height: 1.62;
  color: #707984;
  padding-bottom: 40px;
}

.option {
  position: relative;
}

@media only screen and (max-width: 1024px) {
  .option {
    padding: 0 20px;
  }
}

@media only screen and (max-width: 400px) {
  .option {
    padding: 0;
  }
}

.option:before {
  content: '';
  width: 1000px;
  height: 1000px;
  background-image: linear-gradient(to top, #f2f5fa, #ffffff);
  position: absolute;
  right: -41%;
  border-radius: 100%;
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .option:before {
    display: none;
  }
}

.option .container {
  position: relative;
  z-index: 2;
}

@media only screen and (max-width: 767px) {
  .option__block {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.option__block__img {
  width: 125px;
  height: 125px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 160px;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  background: #ffffff;
  overflow: hidden;
}

.option__block__img img {
  width: 86px;
}

.option__block__img--width img {
  width: 70px;
}

.option__block__text {
  padding-bottom: 50px;
}

.option__block__text h4 {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.5;
  color: #3c4450;
  padding-top: 40px;
  padding-bottom: 20px;
}

.option__block__text p {
  line-height: 1.62;
  color: #707984;
}

.work {
  position: relative;
}

.work:before {
  content: '';
  width: 983px;
  height: 983px;
  background-image: linear-gradient(to bottom, #f2f5fa, #ffffff);
  position: absolute;
  left: -41%;
  top: 60%;
  border-radius: 100%;
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .work:before {
    display: none;
  }
}

.work .container {
  z-index: 55;
}

.work__tabs {
  position: relative;
}

.work__tabs__caption {
  position: relative;
  z-index: 55;
}

@media only screen and (max-width: 1024px) {
  .work__tabs__caption {
    display: flex;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 500px) {
  .work__tabs__caption {
    flex-direction: column;
  }
}

.work__tabs__caption li {
  padding: 30px 40px;
  display: inline-block;
  margin-bottom: 20px;
  transition: all 0.5s;
  font-weight: bold;
  line-height: 2.0;
  font-size: 18px;
  color: #3c4450;
  position: relative;
  cursor: pointer;
}

@media only screen and (max-width: 1024px) {
  .work__tabs__caption li {
    padding: 30px 0;
    width: 48%;
    text-align: center;
    border: solid 1px #dddddd;
  }
}

@media only screen and (max-width: 500px) {
  .work__tabs__caption li {
    width: 100%;
  }
}

.work__tabs__caption li.active {
  background-color: #ffffff;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.15);
}

@media only screen and (max-width: 1024px) {
  .work__tabs__caption li.active {
    border: solid 1px #ffffff;
  }
}

@media only screen and (max-width: 1024px) {
  .work__tabs__caption li.active:before {
    visibility: visible !important;
  }
}

.work__tabs__caption li:before {
  content: '';
  position: absolute;
  right: -9px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-top: 12px solid transparent;
  border-left: 10px solid #ffffff;
  border-bottom: 12px solid transparent;
}

@media only screen and (max-width: 1024px) {
  .work__tabs__caption li:before {
    right: auto;
    top: auto;
    transform: none;
    bottom: -16px;
    left: 50%;
    transform: translateX(-50%);
    transform: rotate(90deg);
    visibility: hidden;
  }
}

@media only screen and (max-width: 500px) {
  .work__tabs__caption li:before {
    display: none;
  }
}

.work__tabs__content {
  opacity: 0;
  padding-left: 80px;
  position: absolute;
  top: 0;
  transition: all 0.3s;
  z-index: 1;
  padding-top: 40px;
}

@media only screen and (max-width: 1024px) {
  .work__tabs__content {
    position: relative;
    display: none;
  }
}

.work__tabs__content.active {
  position: relative;
  opacity: 1;
  display: block;
}

.work__tabs__content h3 {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.5;
  color: #3c4450;
  padding-bottom: 20px;
  position: relative;
}

.work__tabs__content h3 span {
  position: absolute;
  left: -100px;
  background-image: linear-gradient(to bottom, #42d79f, #26c8cb), linear-gradient(#25ce98, #25ce98);
  font-family: 'Roboto';
  font-size: 100px;
  line-height: 0.25;
  text-align: center;
  -webkit-background-clip: text;
  background-clip: text;
  height: 90px;
  color: transparent;
  padding-top: 33px;
}

@media only screen and (max-width: 1024px) {
  .work__tabs__content h3 span {
    left: -70px;
  }
}

.work__tabs__content p {
  line-height: 1.62;
  padding-bottom: 60px;
}

.ads {
  position: relative;
  z-index: 44;
}

.ads__block {
  border-radius: 8px;
  background-color: #ffffff;
  border: solid 1px #dddddd;
  transition: all 0.3s;
}

@media only screen and (max-width: 1024px) {
  .ads__block {
    margin-bottom: 20px;
    box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.15);
  }
}

.ads__block:hover {
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.15);
  border: solid 1px transparent;
}

.ads__block:hover .swiper-pagination-bullet, .ads__block:hover .swiper-button-next, .ads__block:hover .swiper-button-prev {
  opacity: 1 !important;
}

.ads__block__swiper .swiper-container {
  width: 100%;
  height: 100%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.ads__block__swiper .swiper-container .swiper-slide {
  max-height: 245px;
  height: 100%;
  overflow: hidden;
}

.ads__block__swiper .swiper-container .swiper-slide img {
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .ads__block__swiper .swiper-container .swiper-slide {
    max-height: 100%;
    height: auto;
  }
  .ads__block__swiper .swiper-container .swiper-slide img {
    width: 100%;
  }
}

.ads__block__swiper .swiper-container .swiper-pagination-bullet {
  background: #ffffff;
  opacity: 1;
  width: 10px;
  height: 10px;
  opacity: 0;
  transition: all 0.3s;
}

@media only screen and (max-width: 1024px) {
  .ads__block__swiper .swiper-container .swiper-pagination-bullet {
    opacity: 1;
  }
}

.ads__block__swiper .swiper-container .swiper-pagination-bullet-active {
  background: #ffffff;
}

.ads__block__swiper .swiper-container .swiper-button-next, .ads__block__swiper .swiper-container .swiper-button-prev {
  width: 60px;
  height: 100%;
  top: 9%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 0.3s;
}

@media only screen and (max-width: 1024px) {
  .ads__block__swiper .swiper-container .swiper-button-next, .ads__block__swiper .swiper-container .swiper-button-prev {
    opacity: 1;
  }
}

.ads__block__swiper .swiper-container .swiper-button-next i, .ads__block__swiper .swiper-container .swiper-button-prev i {
  font-size: 30px;
  color: #ffffff;
}

.ads__block__swiper .swiper-container .swiper-button-next {
  background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.5));
  right: 0;
}

.ads__block__swiper .swiper-container .swiper-button-prev {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.5), transparent);
  left: 0;
}

.ads__block__swiper .swiper-container .swiper-button-prev i {
  transform: rotate(180deg);
}

.ads__block__content {
  padding: 20px;
}

.ads__block__content__title a {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.39;
  color: #3c4450;
  transition: all 0.3s;
}

.ads__block__content__title a:hover {
