}

.ads__block__content__info {
  border-bottom: 2px solid #dddddd;
  border-top: 2px solid #dddddd;
  margin: 20px 0;
  padding-top: 20px;
  padding-right: 15px;
  position: relative;
}

.ads__block__content__info p {
  font-size: 15px;
  color: #3c4450;
  padding-left: 30px;
