}

.ads__block__content__info p i.icon-location {
  left: 1px;
}

.ads__block__content__info p.ads__block__content__info--lease {
  position: absolute;
  right: 10px;
  top: 20px;
}

.ads__block__content__bottom a {
  font-size: 15px;
  line-height: 2.33;
  color: #3c4450;
