.intro {
  background: url('../img/header__bg.png') no-repeat;
  background-size: cover;
  padding: 180px 0;
  padding-top: 220px;
  position: relative;
  @include r(767) {
    padding-top: 350px; }
  h1 {
    font-size: 36px;
    font-weight: bold;
    line-height: 1.28;
    color: $white;
    padding-bottom: 40px; }
  form {
    display: flex;
    justify-content: center;
    @include r(1024) {
      display: block; }
    select {
      width: 100%;
      background: $white;
      border-radius: 0;
      border: none;
      padding: 25px 0;
      font-size: 15px;
      text-align-last: center;
      text-align: center;
      &:firs-child {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px; } }
    input {
      padding: 25px 75px;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      background-image: linear-gradient(to bottom, $greenish-teal, $tealish-two);
      font-family: Roboto;
      font-size: 15px;
      font-weight: bold;
      color: $white;
      outline: none;
      transition: all 0.3s;
      cursor: pointer;
      &:hover {
        color: $charcoal-grey; }
      @include r(1024) {
        width: 100%;
        padding: 25px 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 5px; } } }
  &__btn {
    position: absolute;
    width: 100%;
    bottom: 0;
    a {
      display: flex;
      justify-content: center;
      padding-bottom: 30px;
      &:hover {
        i {
          color: $greenish-teal; } } }
    i {
      font-size: 40px;
      color: $white; } } }
